@media (max-width: 599px) {
  .container, .footer, .call-to-action {
    margin-left: 1rem;
    margin-right: 1rem; } }

@media (min-width: 600px) {
  .container, .footer, .call-to-action {
    margin-left: 5%;
    margin-right: 5%; } }

@media (min-width: 900px) {
  .container, .footer, .call-to-action {
    margin-left: 10%;
    margin-right: 10%; } }

@media (min-width: 1200px) {
  .container, .footer, .call-to-action {
    margin-left: 15%;
    margin-right: 15%; } }

@media (max-width: 599px) {
  h1 {
    font-size: 2rem; } }

@media (min-width: 600px) {
  h1 {
    font-size: 2.5rem; } }

@media (min-width: 900px) {
  h1 {
    font-size: 3rem; } }

@media (min-width: 1200px) {
  h1 {
    font-size: 4rem; } }



